"use client";

import React from "react";
import { Bell, Search, User } from "lucide-react";
import { ModeToggle } from "@/components/mode-toggle"; // Import the theme toggle button

export function TopBar() {
  return (
    <div className="flex items-center justify-between px-4 py-2 border-b border-border bg-background">
      {/* Left side - Search Bar */}
      <div className="relative">
        <input
          type="text"
          placeholder="Search..."
          className="p-2 pl-8 pr-4 rounded border border-input text-sm focus:outline-none focus:ring-1 focus:ring-ring"
        />
        <Search className="w-4 h-4 absolute left-2 top-2 text-muted-foreground" />
      </div>

      {/* Right side - Icons & Theme Toggle */}
      <div className="flex items-center space-x-4">
        <ModeToggle /> {/* Theme Toggle Button */}
        <Bell className="cursor-pointer" />
        <User className="cursor-pointer" />
      </div>
    </div>
  );
}
